import './App.css';
import  Navbar  from "./components/Navbar";
import { HashRouter as Router, Route, Switch, Link} from "react-router-dom";

function App() {
  return (
      <Router basename={process.env.PUBLIC_URL}>
    <div className="App">
      <Navbar/>
      <Switch>
        <Link to="./"/>
        <Link to="./contact"/>
        <Link to="./about"/>
      </Switch>
    </div>
      </Router>
  );
}

export default App;
